import store from "../store/index.js";

// API 请求根路径
// var root = "http://134.175.16.143:8080/schoolhelp-1.0.1/";
var root = "/schoolhelp/schoolhelp-1.0.1";

// API url路径
var urls = {
	login: `${root}/login`,
	sendMessage: `${root}/user/message`,
	updateUserInfo: `${root}/user`,
	deleteCollect: `${root}/user/collect`
}

// 封装请求方法
var req = {
	request(url, data, method, that, success, fail) {
		uni.request({
			url: url,
			data: data,
			method: method,
			header: {
				//取值：application/json(默认) / application/x-www-form-urlencoded
				'content-type': 'application/json', 
				'token': store.state.token	//默认携带token，未登录时，token为''
			},
			success: (res) => {
				console.log(url, res);
				if (success) success(that, res);
			},
			fail: (err) => {
				if (fail) fail(that, res);
			}
		});
	},
	get(url, data, that, success, fail) {
		this.request(url, data, 'GET', that, success, fail);
	},
	post(url, data, that, success, fail) {
		this.request(url, data, 'POST', that, success, fail);
	},
	put(url, data, that, success, fail) {
		this.request(url, data, 'PUT', that, success, fail);
	},
	del(url, data, that, success, fail) {
		this.request(url, data, 'DELETE', that, success, fail);
	}
}

export default {
	root,
	urls,
	req
}